<script>
  let count = 0;
</script>

<style>
  h1 {
    font-size: 8rem;
    font-weight: 700;
  }

  button {
    display: inline-block;
    padding: 1.5rem 5rem;
    color: inherit;
    font-family: inherit;
    background: transparent;
    border: 1px solid;
  }
</style>

<h1>Count: {count}</h1>

<div>
  <button on:click={() => (count += 1)}>+1</button>
  <button on:click={() => (count -= 1)}>-1</button>
  <button on:click={() => (count = 0)}>Reset</button>
</div>
